	import {  
        mapMutations  
    } from 'vuex';
	import interfaces from '../../utils/interface.js';



	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: true
			}
		},
		onLoad(){
			
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key; //监控输入
				this[key] = e.detail.value; //登录号码
				this.logining = false;
			},
			navBack(){
				uni.navigateBack();
			},
			toRegist(){
				uni.navigateTo({
					url : './register'
				})
			},
			async toLogin(){
				// this.logining = true;
				const {mobile, password} = this;
				
				if(!mobile){
					uni.showToast({
						icon:"none",
						title:"请输入手机号"
					})
					return;
				}
				else if(!password){
					uni.showToast({
						icon:"none",
						title:"请输入密码"
					})
					return;
				}
				else {
					this.request({
						url: interfaces.login,
						method: "POST",
						data: {
							tell : this.mobile,
							password : this.password
						},
						success: ((res) => {
							
							if(res.error_code == 0){
								let session_id = res.session;
								const userInfo = {				
										id: 1,
										mobile: this.mobile,
										nickname: `砍砍用户${this.mobile}`,
										
										portrait: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1584357130382&di=99c11194936cc6efc617433713877360&imgtype=0&src=http%3A%2F%2Fpic2.zhimg.com%2F50%2Fv2-501c31cde297a64e0710b6a2a592b156_hd.jpg'
								}




								this.login(userInfo)
								uni.switchTab({
									url: '../home/home'
								})
								// uni.navigateBack()
								
							}else{
								uni.showToast({
									icon:'none',
									title: '账号或密码错误'
								})
							}			
						})
					});





























					// uni.navigateBack();  
				}
			}
		},

	}
